import React, { Component } from 'react';
import gql from 'graphql-tag';
import { Query } from 'react-apollo';
import Error from './ErrorMessage';
import styled from 'styled-components';
import Head from 'next/head';
import formatMoney from '../lib/formatMoney';

const SingleListingStyles = styled.div`
  max-width: 1200px;
  margin: 2rem auto;
  box-shadow: ${props => props.theme.bs};
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  .details {
    margin: 3rem;
    font-size: 14px;
  }
  .section {
    border: solid black 2px;
  }
  .section h3 {
    display: inline-block;
    margin-top: -10px;
    background: yellow;
  }
`;

const SINGLE_LISTING_QUERY = gql`
  query SINGLE_LISTING_QUERY($id: ID!) {
    listing(where: { id: $id }) {
      address
      description
      mainImage
      rooms
      bath
      lotSize
      houseSize
      price
    }
  }
`;

class SingleListing extends Component {
  render() {
    return (
      <Query
        query={SINGLE_LISTING_QUERY}
        variables={{
          id: this.props.id,
        }}
      >
        {({ error, loading, data }) => {
          if (error) return <Error error={error} />;
          if (loading) return <p>Loading...</p>;
          if (!data.listing) return <p>No listing Found for {this.props.id}</p>;
          const listing = data.listing;
          return (
            <SingleListingStyles>
              <Head>
                <title>Sick Fits | {listing.address}</title>
              </Head>
              <div className="details">
                <h2>{listing.address}</h2>
                <img src={listing.mainImage} alt={listing.description}/>
                <div className="section">
                  <h3>Description</h3>
                  <p>{listing.description}</p>
                </div>
                <div className="section">
                  <h3>House Specifications</h3>
                  <ul>
                    <li>{formatMoney(listing.price)}</li>
                    <li>{listing.bath}</li>
                    <li>{listing.rooms}</li>
                    <li>{listing.lotSize}</li>
                    <li>{listing.houseSize}</li>
                  </ul>
                </div>
                
              </div>
            </SingleListingStyles>
          );
        }}
      </Query>
    );
  }
}

export default SingleListing;
